import React from 'react'
import Chat from './Chat'
import { auth } from './env.local/firebase'
import './HomeScreen.css'
import Sidebar from './Sidebar'

function HomeScreen() {

  const signOut = () =>{
    auth.signOut()
  }

  return (
    <div className="home">
        <div className="Chat_sec">
            <Sidebar />
            <Chat />
        </div>
        <div className="signout" onClick={signOut}><p>Sign Out</p></div>
    </div>
  )
}

export default HomeScreen

